import { css } from 'emotion';
import { Color } from 'reactism/utils';
import { useState } from 'react';

const LightBox = ({ onVisible, setOnVisible }) => {
  
  const baseStyle = css`
    visibility: ${onVisible ? 'visible' : 'collapse'};
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.7);
    z-index: 99;
    display: grid;
    justify-items: center;
    img {
      align-self: center;
      width: 60%;
      z-index: 100;
    }
    .closeLightBox {
        position: absolute;
        right: 20px;
        top: 20px;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        border: 1.5px solid #fff;
        text-align: center;
        font-size: 20px;
        color: #fff;
        align-self: center;
        cursor: pointer;
        &:hover {
          border-color: ${ Color.success };
          color: ${ Color.success };
        }
    }
  `

  const handleCloseLightbox = () => {
    setOnVisible(false);
  }

  return (
    <div className={ baseStyle }>
      <div className="closeLightBox" onClick={ handleCloseLightbox }>
        <span>&times;</span>
      </div>
      <img src="/static/book1.jpg" alt=""/>
    </div>
  )
}

export default LightBox